import { useState, useCallback, useMemo } from 'react'
import PropTypes from 'prop-types'

export default function AdjustmentHistory() {
  const [typeFilter, setTypeFilter] = useState('all')
  const [statusFilter, setStatusFilter] = useState('all')
  const [searchQuery, setSearchQuery] = useState('')

  //   (ACM + ADM )
  const mockData = [
    { id: 1, no: 'ACM-2024-001', type: 'ACM', seller: 'A', reason: ' ', amount: 15000, status: 'approved', date: '2024-01-15' },
    { id: 2, no: 'ADM-2024-001', type: 'ADM', seller: 'A', reason: ' ', amount: -25000, status: 'approved', date: '2024-01-15' },
    { id: 3, no: 'ACM-2024-002', type: 'ACM', seller: 'B', reason: '', amount: 50000, status: 'approved', date: '2024-01-14' },
    { id: 4, no: 'ADM-2024-002', type: 'ADM', seller: 'B', reason: '', amount: -100000, status: 'approved', date: '2024-01-14' },
    { id: 5, no: 'ACM-2024-003', type: 'ACM', seller: 'C', reason: ' ', amount: 30000, status: 'pending', date: '2024-01-15' },
    { id: 6, no: 'ADM-2024-003', type: 'ADM', seller: 'C', reason: ' ', amount: -50000, status: 'pending', date: '2024-01-15' },
    { id: 7, no: 'ACM-2024-004', type: 'ACM', seller: 'D', reason: '', amount: 20000, status: 'rejected', date: '2024-01-14' },
    { id: 8, no: 'ADM-2024-004', type: 'ADM', seller: 'D', reason: ' ', amount: -15000, status: 'rejected', date: '2024-01-14' }
  ]

  const formatCurrency = useCallback((amount) => {
    return new Intl.NumberFormat('ko-KR', {
      style: 'currency',
      currency: 'KRW'
    }).format(Math.abs(amount))
  }, [])

  const getTypeBadge = useCallback((type) => {
    if (type === 'ACM') {
      return <span className="px-2 py-1 rounded-full text-xs font-medium bg-blue-100 text-blue-800"> ACM</span>
    }
    return <span className="px-2 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800"> ADM</span>
  }, [])

  const getStatusBadge = useCallback((status) => {
    const badges = {
      pending: { text: '', color: 'bg-yellow-100 text-yellow-800' },
      approved: { text: '', color: 'bg-green-100 text-green-800' },
      rejected: { text: '', color: 'bg-gray-100 text-gray-800' }
    }
    const badge = badges[status] || badges.pending
    return (
      <span className={`px-2 py-1 rounded-full text-xs font-medium ${badge.color}`}>
        {badge.text}
      </span>
    )
  }, [])

  const filteredData = useMemo(() => {
    return mockData.filter(item => {
      const matchesType = typeFilter === 'all' || item.type === typeFilter
      const matchesStatus = statusFilter === 'all' || item.status === statusFilter
      const matchesSearch = searchQuery === '' || 
        item.no.toLowerCase().includes(searchQuery.toLowerCase()) ||
        item.seller.toLowerCase().includes(searchQuery.toLowerCase()) ||
        item.reason.toLowerCase().includes(searchQuery.toLowerCase())
      return matchesType && matchesStatus && matchesSearch
    })
  }, [typeFilter, statusFilter, searchQuery])

  const stats = useMemo(() => {
    const total = mockData.length
    const acmCount = mockData.filter(item => item.type === 'ACM').length
    const admCount = mockData.filter(item => item.type === 'ADM').length
    const totalAmount = mockData
      .filter(item => item.status === 'approved')
      .reduce((sum, item) => sum + item.amount, 0)
    
    return { total, acmCount, admCount, totalAmount }
  }, [])

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <StatCard title=" " value={stats.total.toString()} icon="" color="text-blue-600" />
        <StatCard title="ACM ()" value={stats.acmCount.toString()} icon="" color="text-green-600" />
        <StatCard title="ADM ()" value={stats.admCount.toString()} icon="" color="text-red-600" />
        <StatCard 
          title="  " 
          value={formatCurrency(stats.totalAmount)} 
          icon="" 
          color={stats.totalAmount >= 0 ? 'text-green-600' : 'text-red-600'} 
        />
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
            <select 
              value={typeFilter}
              onChange={(e) => setTypeFilter(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all"></option>
              <option value="ACM">ACM ()</option>
              <option value="ADM">ADM ()</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"></label>
            <select 
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all"></option>
              <option value="pending"></option>
              <option value="approved"></option>
              <option value="rejected"></option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"></label>
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder=", ,  "
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
        </div>
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredData.map((item) => (
                <tr key={item.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{item.no}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{getTypeBadge(item.type)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{item.seller}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{item.reason}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-semibold">
                    <span className={item.amount >= 0 ? 'text-green-600' : 'text-red-600'}>
                      {item.amount >= 0 ? '+' : '-'}{formatCurrency(item.amount)}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">{getStatusBadge(item.status)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{item.date}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <button className="text-blue-600 hover:text-blue-900">
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

function StatCard({ title, value, icon, color = 'text-blue-600' }) {
  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
      <div className="flex items-center justify-between">
        <div>
          <p className="text-sm text-gray-600">{title}</p>
          <p className={`text-2xl font-bold ${color} mt-1`}>{value}</p>
        </div>
        <div className="text-3xl">{icon}</div>
      </div>
    </div>
  )
}

StatCard.propTypes = {
  title: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired,
  color: PropTypes.string
}

