import { useState, useCallback, useMemo } from 'react'
import PropTypes from 'prop-types'

export default function AdjustmentACM() {
  const [statusFilter, setStatusFilter] = useState('all')
  const [showCreateModal, setShowCreateModal] = useState(false)

  //  
  const mockData = [
    { id: 1, acmNo: 'ACM-2024-001', seller: 'A', reason: ' ', amount: 15000, status: 'pending', createdDate: '2024-01-15', createdBy: '1' },
    { id: 2, acmNo: 'ACM-2024-002', seller: 'B', reason: '', amount: 50000, status: 'approved', createdDate: '2024-01-14', createdBy: '2' },
    { id: 3, acmNo: 'ACM-2024-003', seller: 'C', reason: ' ', amount: 30000, status: 'pending', createdDate: '2024-01-15', createdBy: '1' },
    { id: 4, acmNo: 'ACM-2024-004', seller: 'D', reason: '', amount: 20000, status: 'rejected', createdDate: '2024-01-14', createdBy: '3' },
    { id: 5, acmNo: 'ACM-2024-005', seller: 'A', reason: ' ', amount: 25000, status: 'approved', createdDate: '2024-01-13', createdBy: '2' }
  ]

  const formatCurrency = useCallback((amount) => {
    return new Intl.NumberFormat('ko-KR', {
      style: 'currency',
      currency: 'KRW'
    }).format(amount)
  }, [])

  const getStatusBadge = useCallback((status) => {
    const badges = {
      pending: { text: '', color: 'bg-yellow-100 text-yellow-800' },
      approved: { text: '', color: 'bg-green-100 text-green-800' },
      rejected: { text: '', color: 'bg-red-100 text-red-800' }
    }
    const badge = badges[status] || badges.pending
    return (
      <span className={`px-2 py-1 rounded-full text-xs font-medium ${badge.color}`}>
        {badge.text}
      </span>
    )
  }, [])

  const filteredData = useMemo(() => {
    return mockData.filter(item => statusFilter === 'all' || item.status === statusFilter)
  }, [statusFilter])

  const handleApprove = useCallback((id) => {
    console.log(':', id)
    alert(`ACM ${id} .`)
  }, [])

  const handleReject = useCallback((id) => {
    const reason = prompt('  :')
    if (reason) {
      console.log(':', id, reason)
      alert(`ACM ${id} .`)
    }
  }, [])

  const handleCreateACM = useCallback(() => {
    setShowCreateModal(true)
  }, [])

  const totalPending = useMemo(() => {
    return mockData.filter(item => item.status === 'pending').length
  }, [])

  const totalAmount = useMemo(() => {
    return mockData.reduce((sum, item) => sum + item.amount, 0)
  }, [])

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="flex justify-end">
        <button
          onClick={handleCreateACM}
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
        >
           ACM 
        </button>
      </div>

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <StatCard title=" ACM" value={mockData.length.toString()} icon="" color="text-blue-600" />
        <StatCard title=" " value={totalPending.toString()} icon="" color="text-yellow-600" />
        <StatCard title="  " value={formatCurrency(totalAmount)} icon="" color="text-green-600" />
        <StatCard title=" " value="3" icon="" color="text-purple-600" />
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
        <div className="flex items-center gap-4">
          <label className="text-sm font-medium text-gray-700"> :</label>
          <select 
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="all"></option>
            <option value="pending"></option>
            <option value="approved"></option>
            <option value="rejected"></option>
          </select>
        </div>
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">ACM </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"> </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredData.map((item) => (
                <tr key={item.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{item.acmNo}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{item.seller}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{item.reason}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-semibold text-green-600">{formatCurrency(item.amount)}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{getStatusBadge(item.status)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{item.createdDate}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{item.createdBy}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium space-x-2">
                    <button className="text-blue-600 hover:text-blue-900">
                      
                    </button>
                    {item.status === 'pending' && (
                      <>
                        <button
                          onClick={() => handleApprove(item.id)}
                          className="text-green-600 hover:text-green-900"
                        >
                          
                        </button>
                        <button
                          onClick={() => handleReject(item.id)}
                          className="text-red-600 hover:text-red-900"
                        >
                          
                        </button>
                      </>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/*   ( ) */}
      {showCreateModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-full max-w-md">
            <h3 className="text-xl font-bold text-gray-900 mb-4">ACM </h3>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                <select className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500">
                  <option>A</option>
                  <option>B</option>
                  <option>C</option>
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                <input 
                  type="text" 
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder=":  "
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                <input 
                  type="number" 
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="0"
                />
              </div>
            </div>
            <div className="flex justify-end gap-2 mt-6">
              <button
                onClick={() => setShowCreateModal(false)}
                className="px-4 py-2 text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
              >
                
              </button>
              <button
                onClick={() => {
                  alert('ACM .')
                  setShowCreateModal(false)
                }}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
              >
                
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

function StatCard({ title, value, icon, color = 'text-blue-600' }) {
  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
      <div className="flex items-center justify-between">
        <div>
          <p className="text-sm text-gray-600">{title}</p>
          <p className={`text-2xl font-bold ${color} mt-1`}>{value}</p>
        </div>
        <div className="text-3xl">{icon}</div>
      </div>
    </div>
  )
}

StatCard.propTypes = {
  title: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired,
  color: PropTypes.string
}

