import { useState, useCallback, useMemo } from 'react'
import PropTypes from 'prop-types'

export default function SettlementPayment() {
  const [statusFilter, setStatusFilter] = useState('ready')
  const [selectedItems, setSelectedItems] = useState([])

  //  
  const mockData = [
    { id: 1, invoiceNo: 'INV-2024-001', seller: 'A', account: ' 110-123-456789', amount: 5800000, status: 'ready', approvedDate: '2024-01-15' },
    { id: 2, invoiceNo: 'INV-2024-002', seller: 'B', account: ' 1002-234-567890', amount: 12400000, status: 'ready', approvedDate: '2024-01-15' },
    { id: 3, invoiceNo: 'INV-2024-003', seller: 'C', account: ' 123-45-678901', amount: 3200000, status: 'paid', approvedDate: '2024-01-14' },
    { id: 4, invoiceNo: 'INV-2024-004', seller: 'D', account: ' 333-456-789012', amount: 8900000, status: 'ready', approvedDate: '2024-01-15' },
    { id: 5, invoiceNo: 'INV-2024-005', seller: 'E', account: ' 456-12-345678', amount: 6700000, status: 'processing', approvedDate: '2024-01-14' }
  ]

  const formatCurrency = useCallback((amount) => {
    return new Intl.NumberFormat('ko-KR', {
      style: 'currency',
      currency: 'KRW'
    }).format(amount)
  }, [])

  const getStatusBadge = useCallback((status) => {
    const badges = {
      ready: { text: '', color: 'bg-yellow-100 text-yellow-800' },
      processing: { text: '', color: 'bg-blue-100 text-blue-800' },
      paid: { text: '', color: 'bg-green-100 text-green-800' },
      failed: { text: '', color: 'bg-red-100 text-red-800' }
    }
    const badge = badges[status] || badges.ready
    return (
      <span className={`px-2 py-1 rounded-full text-xs font-medium ${badge.color}`}>
        {badge.text}
      </span>
    )
  }, [])

  const filteredData = useMemo(() => {
    return mockData.filter(item => statusFilter === 'all' || item.status === statusFilter)
  }, [statusFilter])

  const handleSelectAll = useCallback((e) => {
    if (e.target.checked) {
      //       (ready ) 
      const selectableItems = filteredData.filter(item => item.status === 'ready')
      setSelectedItems(selectableItems.map(item => item.id))
    } else {
      setSelectedItems([])
    }
  }, [filteredData])

  const selectableItemsCount = useMemo(() => {
    return filteredData.filter(item => item.status === 'ready').length
  }, [filteredData])

  const handleSelectItem = useCallback((id) => {
    setSelectedItems(prev => 
      prev.includes(id) ? prev.filter(itemId => itemId !== id) : [...prev, id]
    )
  }, [])

  const handleProcessPayment = useCallback(() => {
    if (selectedItems.length === 0) {
      alert('  .')
      return
    }
    const totalAmount = mockData
      .filter(item => selectedItems.includes(item.id))
      .reduce((sum, item) => sum + item.amount, 0)
    
    const confirmed = confirm(
      ` ${selectedItems.length}  ?\n : ${formatCurrency(totalAmount)}`
    )
    
    if (confirmed) {
      console.log(' :', selectedItems)
      alert('  .')
      setSelectedItems([])
    }
  }, [selectedItems, formatCurrency])

  const totalReady = useMemo(() => {
    return mockData.filter(item => item.status === 'ready').length
  }, [])

  const totalAmount = useMemo(() => {
    return mockData.filter(item => item.status === 'ready').reduce((sum, item) => sum + item.amount, 0)
  }, [])

  const selectedAmount = useMemo(() => {
    return mockData.filter(item => selectedItems.includes(item.id)).reduce((sum, item) => sum + item.amount, 0)
  }, [selectedItems])

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <StatCard title=" " value={totalReady.toString()} icon="" color="text-yellow-600" />
        <StatCard title=" " value={formatCurrency(totalAmount)} icon="" color="text-blue-600" />
        <StatCard title=" " value={selectedItems.length.toString()} icon="" color="text-green-600" />
        <StatCard title=" " value={formatCurrency(selectedAmount)} icon="" color="text-purple-600" />
      </div>

      {/*    */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div className="flex items-center gap-4">
            <label className="text-sm font-medium text-gray-700"> :</label>
            <select 
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all"></option>
              <option value="ready"></option>
              <option value="processing"></option>
              <option value="paid"></option>
              <option value="failed"></option>
            </select>
          </div>
          <button
            onClick={handleProcessPayment}
            disabled={selectedItems.length === 0}
            className={`px-6 py-2 rounded-lg font-medium transition-colors ${
              selectedItems.length > 0 
                ? 'bg-blue-600 text-white hover:bg-blue-700' 
                : 'bg-gray-300 text-gray-500 cursor-not-allowed'
            }`}
          >
              ({selectedItems.length})
          </button>
        </div>
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left">
                  <input
                    type="checkbox"
                    onChange={handleSelectAll}
                    checked={selectableItemsCount > 0 && selectedItems.length === selectableItemsCount}
                    className="rounded border-gray-300"
                  />
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"> </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredData.map((item) => (
                <tr key={item.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap">
                    {item.status === 'ready' && (
                      <input
                        type="checkbox"
                        checked={selectedItems.includes(item.id)}
                        onChange={() => handleSelectItem(item.id)}
                        className="rounded border-gray-300"
                      />
                    )}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{item.invoiceNo}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{item.seller}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-600">{item.account}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-semibold text-gray-900">{formatCurrency(item.amount)}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{getStatusBadge(item.status)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{item.approvedDate}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <button className="text-blue-600 hover:text-blue-900">
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

function StatCard({ title, value, icon, color = 'text-blue-600' }) {
  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
      <div className="flex items-center justify-between">
        <div>
          <p className="text-sm text-gray-600">{title}</p>
          <p className={`text-2xl font-bold ${color} mt-1`}>{value}</p>
        </div>
        <div className="text-3xl">{icon}</div>
      </div>
    </div>
  )
}

StatCard.propTypes = {
  title: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired,
  color: PropTypes.string
}

