import { useState, useCallback, useMemo } from 'react'
import PropTypes from 'prop-types'
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts'

export default function ReconciliationPG() {
  const [selectedPeriod, setSelectedPeriod] = useState('today')
  const [statusFilter, setStatusFilter] = useState('all')

  //  
  const mockData = [
    { id: 1, date: '2024-01-15', pg: 'KG', systemAmount: 15000000, pgAmount: 15000000, difference: 0, status: 'matched' },
    { id: 2, date: '2024-01-15', pg: '', systemAmount: 28500000, pgAmount: 28450000, difference: 50000, status: 'mismatch' },
    { id: 3, date: '2024-01-15', pg: '', systemAmount: 12300000, pgAmount: 12300000, difference: 0, status: 'matched' },
    { id: 4, date: '2024-01-14', pg: 'KG', systemAmount: 18200000, pgAmount: 18200000, difference: 0, status: 'matched' },
    { id: 5, date: '2024-01-14', pg: '', systemAmount: 32100000, pgAmount: 32000000, difference: 100000, status: 'mismatch' }
  ]

  const chartData = [
    { name: 'KG', system: 33200000, pg: 33200000 },
    { name: '', system: 60600000, pg: 60450000 },
    { name: '', system: 12300000, pg: 12300000 }
  ]

  const formatCurrency = useCallback((amount) => {
    return new Intl.NumberFormat('ko-KR', {
      style: 'currency',
      currency: 'KRW'
    }).format(amount)
  }, [])

  const getStatusBadge = useCallback((status) => {
    if (status === 'matched') {
      return <span className="px-2 py-1 rounded-full text-xs font-medium bg-green-100 text-green-800"> </span>
    }
    return <span className="px-2 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800"> </span>
  }, [])

  const filteredData = useMemo(() => {
    return mockData.filter(item => statusFilter === 'all' || item.status === statusFilter)
  }, [statusFilter])

  const stats = useMemo(() => {
    const matched = mockData.filter(item => item.status === 'matched').length
    const mismatch = mockData.filter(item => item.status === 'mismatch').length
    const totalDifference = mockData.reduce((sum, item) => sum + Math.abs(item.difference), 0)
    
    return { matched, mismatch, totalDifference }
  }, [])

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <StatCard title=" " value={mockData.length.toString()} icon="" color="text-blue-600" />
        <StatCard title="" value={stats.matched.toString()} icon="" color="text-green-600" />
        <StatCard title="" value={stats.mismatch.toString()} icon="" color="text-red-600" />
        <StatCard title=" " value={formatCurrency(stats.totalDifference)} icon="" color="text-yellow-600" />
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">PG  </h3>
        <ResponsiveContainer width="100%" height={300}>
          <BarChart data={chartData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="name" />
            <YAxis />
            <Tooltip formatter={(value) => formatCurrency(value)} />
            <Legend />
            <Bar dataKey="system" fill="#3b82f6" name=" " />
            <Bar dataKey="pg" fill="#10b981" name="PG " />
          </BarChart>
        </ResponsiveContainer>
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
            <select 
              value={selectedPeriod}
              onChange={(e) => setSelectedPeriod(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="today"></option>
              <option value="week"> 7</option>
              <option value="month"> </option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"></label>
            <select 
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all"></option>
              <option value="matched"></option>
              <option value="mismatch"></option>
            </select>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">PG</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"> </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">PG </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredData.map((item) => (
                <tr key={item.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{item.date}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{item.pg}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{formatCurrency(item.systemAmount)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{formatCurrency(item.pgAmount)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm">
                    <span className={item.difference === 0 ? 'text-gray-500' : 'text-red-600 font-semibold'}>
                      {item.difference === 0 ? '-' : formatCurrency(Math.abs(item.difference))}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">{getStatusBadge(item.status)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                    <button className="text-blue-600 hover:text-blue-900">
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

function StatCard({ title, value, icon, color = 'text-blue-600' }) {
  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
      <div className="flex items-center justify-between">
        <div>
          <p className="text-sm text-gray-600">{title}</p>
          <p className={`text-2xl font-bold ${color} mt-1`}>{value}</p>
        </div>
        <div className="text-3xl">{icon}</div>
      </div>
    </div>
  )
}

StatCard.propTypes = {
  title: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired,
  color: PropTypes.string
}

