import { useState, useCallback, useMemo } from 'react'
import PropTypes from 'prop-types'

export default function SellerPortalMyInvoices() {
  const [statusFilter, setStatusFilter] = useState('all')
  const [selectedPeriod, setSelectedPeriod] = useState('month')

  //  
  const mockData = [
    { id: 1, invoiceNo: 'INV-2024-001', period: '2024-01', amount: 5800000, items: 38, status: 'confirmed', issuedDate: '2024-01-10', paidDate: '2024-01-15' },
    { id: 2, invoiceNo: 'INV-2023-012', period: '2023-12', amount: 6200000, items: 42, status: 'paid', issuedDate: '2023-12-10', paidDate: '2023-12-15' },
    { id: 3, invoiceNo: 'INV-2023-011', period: '2023-11', amount: 5400000, items: 35, status: 'paid', issuedDate: '2023-11-10', paidDate: '2023-11-15' },
    { id: 4, invoiceNo: 'INV-2024-002', period: '2024-01', amount: 7100000, items: 48, status: 'sent', issuedDate: '2024-01-25', paidDate: null },
    { id: 5, invoiceNo: 'INV-2023-010', period: '2023-10', amount: 4900000, items: 31, status: 'paid', issuedDate: '2023-10-10', paidDate: '2023-10-15' }
  ]

  const formatCurrency = useCallback((amount) => {
    return new Intl.NumberFormat('ko-KR', {
      style: 'currency',
      currency: 'KRW'
    }).format(amount)
  }, [])

  const getStatusBadge = useCallback((status) => {
    const badges = {
      sent: { text: ' ', color: 'bg-yellow-100 text-yellow-800' },
      confirmed: { text: ' ', color: 'bg-blue-100 text-blue-800' },
      disputed: { text: '', color: 'bg-red-100 text-red-800' },
      paid: { text: ' ', color: 'bg-green-100 text-green-800' }
    }
    const badge = badges[status] || badges.sent
    return (
      <span className={`px-2 py-1 rounded-full text-xs font-medium ${badge.color}`}>
        {badge.text}
      </span>
    )
  }, [])

  const filteredData = useMemo(() => {
    return mockData.filter(item => statusFilter === 'all' || item.status === statusFilter)
  }, [statusFilter])

  const stats = useMemo(() => {
    const totalAmount = mockData.reduce((sum, item) => sum + item.amount, 0)
    const pending = mockData.filter(item => item.status === 'sent').length
    const thisMonth = mockData.filter(item => item.period === '2024-01').reduce((sum, item) => sum + item.amount, 0)
    
    return { totalAmount, pending, thisMonth }
  }, [])

  const handleConfirm = useCallback((id) => {
    console.log(' :', id)
    alert(' .')
  }, [])

  const handleDispute = useCallback((id) => {
    const reason = prompt('  :')
    if (reason) {
      console.log(':', id, reason)
      alert(' .')
    }
  }, [])

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <StatCard title="  " value={formatCurrency(stats.totalAmount)} icon="" color="text-blue-600" />
        <StatCard title=" " value={stats.pending.toString()} icon="" color="text-yellow-600" />
        <StatCard title=" " value={formatCurrency(stats.thisMonth)} icon="" color="text-green-600" />
        <StatCard title=" " value={mockData.length.toString()} icon="" color="text-purple-600" />
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
            <select 
              value={selectedPeriod}
              onChange={(e) => setSelectedPeriod(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="month"> 1</option>
              <option value="3months"> 3</option>
              <option value="6months"> 6</option>
              <option value="year"> 1</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2"></label>
            <select 
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="all"></option>
              <option value="sent"> </option>
              <option value="confirmed"> </option>
              <option value="disputed"></option>
              <option value="paid"> </option>
            </select>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"> </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"> </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"> </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"> </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"></th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredData.map((item) => (
                <tr key={item.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{item.invoiceNo}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{item.period}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-semibold text-gray-900">{formatCurrency(item.amount)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">{item.items}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{getStatusBadge(item.status)}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{item.issuedDate}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{item.paidDate || '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium space-x-2">
                    <button className="text-blue-600 hover:text-blue-900">
                      
                    </button>
                    {item.status === 'sent' && (
                      <>
                        <button
                          onClick={() => handleConfirm(item.id)}
                          className="text-green-600 hover:text-green-900"
                        >
                          
                        </button>
                        <button
                          onClick={() => handleDispute(item.id)}
                          className="text-red-600 hover:text-red-900"
                        >
                          
                        </button>
                      </>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

function StatCard({ title, value, icon, color = 'text-blue-600' }) {
  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-4">
      <div className="flex items-center justify-between">
        <div>
          <p className="text-sm text-gray-600">{title}</p>
          <p className={`text-2xl font-bold ${color} mt-1`}>{value}</p>
        </div>
        <div className="text-3xl">{icon}</div>
      </div>
    </div>
  )
}

StatCard.propTypes = {
  title: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  icon: PropTypes.string.isRequired,
  color: PropTypes.string
}

